/* Base Styles
ΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇô */
/* NOTE
html is set to 62.5% so that all the REM measurements throughout Skeleton
are based on 10px sizing. So basically 1.5rem = 15px :) */
html {
  font-size: 62.5%;
  /* 62.5 %*/ }

body {
  font-family: "ff-tisa-web-pro", Georgia, serif;
  font-weight: 400;
  font-size: 1.5em;
  /* currently ems cause chrome bug misinterpreting rems on body element */
  line-height: 1.46;
  font-weight: 400; }

/* Typography
ΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇôΓÇô */
h1, h2, h3, h4, h5, h6 {
  -webkit-margin-before: 0em;
  -webkit-margin-after: 0em;
  margin-bottom: 3rem; }

h3, h4 {
  margin-top: 5rem; }

p {
  margin-bottom: 2rem; }

h1 {
  font-family: "abril-titling-condensed", Georgia, serif;
  font-size: 4.0rem;
  line-height: 1.2;
  letter-spacing: -.1rem; }

h2 {
  font-family: "effra", "Arial Black", Gadget, sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  font-size: 3.6rem;
  line-height: 1.25;
  letter-spacing: -.1rem; }

h3 {
  font-family: "abril-titling-condensed", Georgia, serif;
  font-size: 3.0rem;
  line-height: 1.3;
  letter-spacing: -.1rem; }

h4 {
  font-family: "effra", "Arial Black", Gadget, sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  font-size: 2.4rem;
  line-height: 1.35;
  letter-spacing: -.08rem; }

h5 {
  font-family: "ff-tisa-web-pro", Georgia, serif;
  font-style: italic;
  font-size: 1.8rem;
  line-height: 1.5;
  letter-spacing: -.05rem; }

/* Larger than phablet */
@media (min-width: 640px) {
  h1 {
    font-size: 10.0rem;
    line-height: 1; }

  h2 {
    font-size: 3.8rem;
    line-height: 1.15; }

  h3 {
    font-size: 4.0rem;
    line-height: .95; }

  h4 {
    font-size: 3.0rem;
    line-height: 1.26; }

  h5 {
    font-size: 3.0rem;
    line-height: 1.26; }

  p {
    font-size: 2.6rem;
    line-height: 1.46; } }
